/**	01. Resets
*************************************************** **/
button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

button {
  background: none;
  border: 0;
  margin: 0;
  padding: 0;
  cursor: pointer;
}

img {
  border: 0;
  vertical-align: top;
}

input:-webkit-autofill {
  color: #ffffff !important;
}

