.iconlist {
  padding-left: 0;
  margin-left: 1.75em;
  list-style-type: none;
}

.iconlist li {
  position: relative;
}

.iconlist ul {
  list-style-type: none;
  margin: 5px 0 5px 25px;
}

.iconlist > li [class*=" icon-"], .iconlist > li [class^=icon-] {
  position: absolute;
  left: -1.75em;
