/**	17. Social icons
*************************************************** **/
.social-icon {
  margin: 0 5px 5px 0;
  width: 40px;
  height: 40px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  border-color: transparent;
  overflow: hidden;
  display: inline-block;
  text-decoration: none !important;
  text-align: center;
  cursor: pointer;
  font-style: normal;
  letter-spacing: 0em !important;
  color: #eaeaea !important;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}

.social-icon:hover > i {
  color: #fff;
}

.social-icon i {
  display: block;
  position: relative;
  width: 40px;
  height: 40px;
  margin-top: 6px;
  font-size: 28px;
}

.social-icon:hover i:first-child {
  margin-top: -38px;
}

