/** 35. Process Steps
*************************************************** **/
.process-wizard {
  padding: 0 0 10px 0;
}

.process-wizard > .process-wizard-step {
  padding: 0;
  position: relative;
}

.process-wizard > .process-wizard-step .process-wizard-stepnum {
  color: #595959;
  font-size: 16px;
  margin-bottom: 5px;
}

.process-wizard > .process-wizard-step .process-wizard-info {
  color: #999;
  font-size: 14px;
}

.process-wizard > .process-wizard-step > .process-wizard-dot {
  position: absolute;
  width: 30px;
  height: 30px;
  display: block;
  background: #fbe8aa;
  top: 47px;
  left: 50%;
  margin-top: -15px;
  margin-left: -15px;
  border-radius: 50%;
}

.process-wizard > .process-wizard-step > .process-wizard-dot:after {
  content: ' ';
  width: 14px;
  height: 14px;
  background: #fbbd19;
  border-radius: 50px;
  position: absolute;
  top: 8px;
  left: 8px;
}

.process-wizard > .process-wizard-step > .progress {
  position: relative;
  border-radius: 0px;
  height: 8px;
  box-shadow: none;
  margin: 20px 0;
}

.process-wizard > .process-wizard-step > .progress > .progress-bar {
  width: 0px;
  box-shadow: none;
  background: #fbe8aa;
}

.process-wizard > .process-wizard-step.complete > .progress > .progress-bar {
  width: 100%;
}

.process-wizard > .process-wizard-step.active > .progress > .progress-bar {
  width: 50%;
}

.process-wizard > .process-wizard-step:first-child.active > .progress > .progress-bar {
  width: 0%;
}

.process-wizard > .process-wizard-step:last-child.active > .progress > .progress-bar {
  width: 100%;
}

.process-wizard > .process-wizard-step.disabled > .process-wizard-dot {
  background-color: #f5f5f5;
}

.process-wizard > .process-wizard-step.disabled > .process-wizard-dot:after {
  opacity: 0;
}

.process-wizard > .process-wizard-step:first-child > .progress {
  left: 50%;
  width: 50%;
}

.process-wizard > .process-wizard-step:last-child > .progress {
  width: 50%;
}

.process-wizard > .process-wizard-step.disabled a.process-wizard-dot {
  pointer-events: none;
}

/* default */
.process-wizard-default > .process-wizard-step > .process-wizard-dot, .process-wizard-default > .process-wizard-step > .progress > .progress-bar {
  background: #ccc;
}

.process-wizard-default > .process-wizard-step > .process-wizard-dot:after {
  background-color: #666;
}

/* info */
.process-wizard-info > .process-wizard-step > .process-wizard-dot, .process-wizard-info > .process-wizard-step > .progress > .progress-bar {
  background: #d9edf7;
}

.process-wizard-info > .process-wizard-step > .process-wizard-dot:after {
  background-color: #31708f;
}

/* warning */
